import {
  REQUEST_FETCH_SCHOOLS,
  SUCCESS_FETCH_SCHOOLS,
  FAILD_FETCH_SCHOOLS,
  SUCCESS_PATCH_SCHOOL,
  FAILD_PATCH_SCHOOL,
  RETRIEVE_SCHOOLS,
  RETRIEV_SCHOOL,
} from "../typesOfActions/typesOfActions";
import axios from "axios";
export const fetchSchool = (id) => {
  return async (dispatch) => {
    try {
      const { data } = await axios.get(
        `http://localhost:3000/api/schools/${id}`
      );
      dispatch({
        type: RETRIEV_SCHOOL,
        payload: data,
      });
    } catch (err) {
      dispatch({
        type: FAILD_FETCH_SCHOOLS,
        payload: err.message,
      });
    }
  };
};
export const fetchSchools = () => {
  return async (dispatch) => {
    try {
      const { data } = await axios.get("http://localhost:3000/api/schools");
      dispatch({
        type: REQUEST_FETCH_SCHOOLS,
      });
      dispatch({
        type: SUCCESS_FETCH_SCHOOLS,
        payload: data,
      });
    } catch (err) {
      dispatch({
        type: FAILD_FETCH_SCHOOLS,
        payload: err.message,
      });
    }
  };
};

export const editSchool = (id, status) => {
  return async (dispatch) => {
    try {
      await axios
        .patch(`http://localhost:3000/api/schools/${id}`, {
          status,
        })
        .then((data) => {
          console.log(data);
          dispatch({
            type: SUCCESS_PATCH_SCHOOL,
          });
        });
    } catch (err) {
      dispatch({
        type: FAILD_PATCH_SCHOOL,
        payload: err.message,
      });
    }
  };
};

export const retrieveSchool = (schoolsType) => {
  return {
    type: RETRIEVE_SCHOOLS,
    payload: schoolsType,
  };
};
