import React, { useState, useEffect } from "react";
import { useHistory, useParams } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { fetchSchool } from "../../redux/actions/asyncActions";
import * as styles from "./singleSchool.module.scss";
const SingleSchool = () => {
  const history = useHistory();
  const { id } = useParams();
  const { school } = useSelector(({ schoolsReducer }) => schoolsReducer);
  const dispatch = useDispatch();
  const [isClipped, setIsClipped] = useState(true);
  const handleIsClipped = () => {
    setIsClipped(false);
    history.push("/");
  };
  useEffect(() => {
    dispatch(fetchSchool(id));
    // eslint-disable-next-line
  }, [id]);
  return (
    <div className={isClipped ? "is-clipped" : ""}>
      <div className={`modal ${isClipped && "is-active"}`}>
        <div className="modal-background"></div>
        <div className="modal-content">
          {school.documents &&
            school.documents.map((doc, idx) => {
              return (
                <iframe
                  className={styles.frame}
                  title={doc.name}
                  key={idx}
                  src={doc.url}
                />
              );
            })}
        </div>
        <button
          className="modal-close is-large"
          aria-label="close"
          onClick={handleIsClipped}
        ></button>
      </div>
    </div>
  );
};

export default SingleSchool;
