import {
  REQUEST_FETCH_SCHOOLS,
  SUCCESS_FETCH_SCHOOLS,
  FAILD_FETCH_SCHOOLS,
  REQUEST_PATCH_SCHOOL,
  SUCCESS_PATCH_SCHOOL,
  FAILD_PATCH_SCHOOL,
  RETRIEVE_SCHOOLS,
  RETRIEV_SCHOOL,
} from "../typesOfActions/typesOfActions";

const initialState = {
  loading: false,
  schools: [],
  err: "",
  schoolsType: "pending",
  school: {},
};

export const schoolsReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case REQUEST_FETCH_SCHOOLS:
      return {
        ...state,
        loading: true,
      };
    case RETRIEV_SCHOOL:
      return {
        ...state,
        loading: false,
        school: payload,
      };
    case SUCCESS_FETCH_SCHOOLS:
      return {
        ...state,
        schools: payload,
        loading: false,
      };

    case FAILD_FETCH_SCHOOLS:
      return {
        ...state,
        err: payload,
        loading: false,
      };
    case REQUEST_PATCH_SCHOOL:
      return {
        ...state,
        loading: true,
      };
    case SUCCESS_PATCH_SCHOOL:
      return {
        ...state,
        loading: false,
      };
    case FAILD_PATCH_SCHOOL:
      return {
        ...state,
        loading: false,
        err: payload,
      };
    case RETRIEVE_SCHOOLS:
      return {
        ...state,
        loading: false,
        schoolsType: payload,
      };
    default:
      return state;
  }
};
