import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { retrieveSchool } from "../../redux/actions/asyncActions";
import * as styles from "./sidebar.module.scss";
import { MdDashboard, MdPeople } from "react-icons/md";
export const Sidebar = () => {
  const [active, setActive] = useState("pending");
  const dispatch = useDispatch();

  const retirevAcceptedchools = (status) => {
    setActive(status);
    dispatch(retrieveSchool(status));
  };
  return (
    <aside className={styles.dashboard}>
      <div>
        <div className={styles.logo}>
          <span className={styles.logo__name}>Logo</span>
        </div>
        <h2 className={styles.dashboard__title}>
          <MdDashboard />
          <span>Dashboard</span>
        </h2>
      </div>
      <ul className="menu-list">
        <li
          className={active === "pending" ? `${styles.active}` : ""}
          onClick={() => retirevAcceptedchools("pending")}
        >
          <MdPeople />
          <span>requested</span>
        </li>
        <li
          className={active === "accepted" ? `${styles.active}` : ""}
          onClick={() => retirevAcceptedchools("accepted")}
        >
          <MdPeople />
          <span>accepted</span>
        </li>
        <li
          className={active === "rejected" ? `${styles.active}` : ""}
          onClick={() => retirevAcceptedchools("rejected")}
        >
          <MdPeople />
          <span>rejected</span>
        </li>
      </ul>
    </aside>
  );
};
