import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import { editSchool } from "../../redux/actions/asyncActions";
import { FaCheck, FaTimes, FaFileAlt } from "react-icons/fa";
import { Loader } from "../Loader/Loader";
import * as styles from "./main.module.scss";
export const Main = () => {
  const { loading, schools, err, schoolsType } = useSelector(
    ({ schoolsReducer }) => schoolsReducer
  );

  const dispatch = useDispatch();
  const addToAcceptedSchools = (id, status) => {
    return dispatch(editSchool(id, status));
  };
  return (
    <div className={`container ${styles.main}`}>
      <h1 className={styles.main_title}>Company certificates Validation</h1>
      {loading ? (
        <Loader />
      ) : (
        <table className="table">
          <thead>
            <tr>
              <th>School Name</th>
              <th>Certificate Name</th>
              <th>Certificate Status</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            {schools.filter((school) => school.status === schoolsType).length >
            0 ? (
              schools
                .filter((school) => school.status === schoolsType)
                .map((school) => {
                  const { id, name, status, documents } = school;
                  return (
                    <tr key={id}>
                      <td>{name}</td>
                      <td>
                        {documents.map((document, idx) => (
                          <span key={idx}>{document.name}</span>
                        ))}
                      </td>
                      <td>{status}</td>
                      <td>
                        {(status === "accepted" || status === "pending") && (
                          <span
                            className={styles.accept}
                            onClick={() => addToAcceptedSchools(id, "accepted")}
                          >
                            <FaCheck />
                          </span>
                        )}
                        {(status === "rejected" || status === "pending") && (
                          <span
                            className={styles.reject}
                            onClick={() => addToAcceptedSchools(id, "rejected")}
                          >
                            <FaTimes />
                          </span>
                        )}
                        <span className={styles.file}>
                          <Link to={`/${id}`}>
                            <FaFileAlt />
                          </Link>
                        </span>
                      </td>
                    </tr>
                  );
                })
            ) : (
              <tr>
                <td>there is no requested schools</td>
              </tr>
            )}
          </tbody>
        </table>
      )}
      {err && <h2>{err}</h2>}
    </div>
  );
};
